@function vw($px-vw, $base-vw: 1920px) {
  @return ($px-vw * 100vw) / $base-vw;
} 

* {
  user-select: none;
}

::-webkit-scrollbar {
  width: vw(1px);
  background: #ffffff50;
}

::-webkit-scrollbar-thumb {
  background: #fffffffd;
}

.App {
  background: radial-gradient(50% 158.02% at 50% 54.86%, rgba(15, 15, 15, 0) 0%, rgba(30, 30, 34, 0.56) 69.76%, #1E1E21 100%);
  width: 100vw;
  height: 100vh;
  > .label-store {
    position: absolute;
    width: vw(149px);
    height: vw(58px);
    left: vw(1708px);
    top: vw(52px);
    font-family: Montserrat;
    font-style: normal;
    font-weight: 800;
    font-size: vw(24px);
    line-height: vw(29px);
    text-align: right;
    color: #FFFFFF;
    > a {
      font-weight: 600;
    }
  }
  > .slider-right {
    position: absolute;
    width: vw(36px);
    height: vw(391px);
    left: vw(1842px);
    top: vw(139px);
    background: url("./assets/slider-right.svg");
    background-size: 100% 100%;
  }
  > .slider {
    position: absolute;
    width: vw(91px);
    height: vw(844px);
    left: vw(57px);
    top: vw(59px);
    background-image: url("./assets/slidr.svg");
    background-size: 100% 100%;
  }
  > .nav-icon {
    width: vw(45px);
    height: vw(45px);
    position: absolute;
    background-size: 100% 100%;
  }
  #up {
    background-image: url("./assets/up.svg");
    left: vw(35px);
    top: vw(910px);
  }
  #down {
    background-image: url("./assets/down.svg");
    left: vw(35px);
    top: vw(965px);
  }
  > .left {
    position: absolute;
    width: vw(527px);
    height: vw(922px);
    left: vw(35px);
    top: vw(88px);
    > .search-container {
      position: relative;
      top: vw(0px);
      left: vw(181px);
      width: vw(346px);
      height: vw(100px);
      > .search {
        position: relative;
        width: vw(346px);
        height: vw(50px);
        > input {
          top: 0;
          margin: 0;
          position: absolute;
          width: vw(346px);
          height: vw(50px - 24px);
          border: none;
          outline: none;
          padding: 0;
          font-size: vw(10px);
          background: rgba(255, 255, 255, 0.05);
          border: vw(1px) solid rgba(255, 255, 255, 0.15);
          box-sizing: border-box; 
          font-family: Montserrat;
          font-size: vw(14px);
          font-style: normal;
          font-weight: 600;
          line-height: vw(17px);
          letter-spacing: 0em;
          text-align: left;
          padding: vw(24px);
          color: white;
        }
        > input::placeholder {
          color: rgba(255, 255, 255, 0.25);
        }
      }
      > .criterias {
        margin-top: vw(6px);
        display: grid;
        grid-template-columns: repeat(2, vw(170px));
        gap: vw(6px);
        > .criteria {
          background: rgba(255, 255, 255, 0.05);
          border: vw(1px) solid rgba(255, 255, 255, 0.15);
          box-sizing: border-box;
          width: vw(170px);
          height: vw(44px);
          display: flex;
          align-items: center;
          padding: vw(16px);
          font-family: Montserrat;
          font-size: vw(14px);
          font-style: normal;
          font-weight: 600;
          line-height: vw(17px);
          letter-spacing: 0em;
          text-align: left;
          color: rgba(255, 255, 255, 0.25);
          > .icon {
            width: vw(13px);
            height: vw(8px);
            background-size: cover;
          }
          > .icon.up {
            background-image: url("./assets/up-arrow.svg");
          }
          > .icon.down {
            background-image: url("./assets/down-arrow.svg");
          }
        }
        > .criteria.active {
          background: #FFFFFF;
          border: vw(1px) solid rgba(255, 255, 255, 0.15);
          box-sizing: border-box;
          box-shadow: 0px 0px vw(28px) rgba(255, 255, 255, 0.25); 
          color: #252525;
          > img {
            filter: invert(5%) sepia(39%) saturate(22%) hue-rotate(314deg) brightness(101%) contrast(79%);
          }
          > .icon.up {
            background-image: url("./assets/up-arrow-active.svg");
          }
          > .icon.down {
            background-image: url("./assets/down-arrow-active.svg");
          }
        }
      }
    }
    > .items {
      width: vw(346px);
      height: vw(789px);
      position: relative;
      left: vw(181px);
      top: vw(37px);
      overflow: scroll;
      overflow-x: hidden;
      padding-right: vw(6px);
      > .noClothingAvailable {
        font-family: Montserrat;
        font-size: vw(17px);
        font-style: normal;
        font-weight: 600;
        line-height: vw(17px);
        color: #FFFFFF;
        position: absolute;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
      }
      > div {
        > .item {
          font-family: Montserrat;
          font-size: vw(14px);
          font-style: normal;
          font-weight: 600;
          line-height: vw(17px);
          letter-spacing: 0em;
          text-align: left;
          width: vw(346px);
          height: vw(47px);
          background: rgba(255, 255, 255, 0.05);
          border: vw(1px) solid rgba(255, 255, 255, 0.15);
          box-sizing: border-box;
          margin-bottom: vw(6px);
          padding: vw(15px) vw(15px) vw(15px) vw(20px);
          color: #FFFFFF;
          > .price {
            color: #2FFF9B;
            font-family: Montserrat;
            font-size: vw(14px);
            font-style: normal;
            font-weight: 400;
            line-height: vw(17px);
            letter-spacing: 0em;
            text-align: left;
            float: right;
          }
        }
        > .item.active {
          background: #FFFFFF;
          border: vw(1px) solid rgba(255, 255, 255, 0.15);
          box-sizing: border-box;
          box-shadow: 0px 0px vw(5px) rgba(255, 255, 255, 0.25);
          color: #252525;
          > .price {
            color: #252525;
            font-weight: 600;
          }
        }
      }
    }
    > .parts {
      position: absolute;
      width: vw(100px);
      height: vw(922px);
      left: vw(59px);
      top: 0;
      overflow-y: scroll;
      &::-webkit-scrollbar {
        width: 0;
        height: 0;
      }
      > .part {
        position: relative;
        width: vw(100px);
        height: vw(100px);
        background: rgba(255, 255, 255, 0.05);
        border: vw(1px) solid rgba(255, 255, 255, 0.15);
        box-sizing: border-box;
        > .icon-cont {
          display: flex;
          justify-content: center;
          align-items: center;
          width: 100%;
          height: 100%;
          > .icon {
            height: vw(24px);
            position: relative;
            top: vw(-7px);
          }
        }
        > .label {
          position: absolute;
          bottom: vw(11px);
          width: 100%;
          text-align: center;
          font-family: Montserrat;
          font-size: vw(14px);
          font-style: normal;
          font-weight: 600;
          line-height: vw(17px);
          letter-spacing: 0em;
          text-align: left;
          color: white;
          text-align: center;
        }
      }
      > .part.active {   
        background: #FFFFFF;
        border: vw(1px) solid rgba(255, 255, 255, 0.15);
        box-shadow: 0px 0px vw(28px) rgba(255, 255, 255, 0.25);
        > .icon-cont .icon {
          filter: invert(1);
        }
        > .label {
          color: black;
        }
      }
      > .spacer {
        width: vw(1px);
        height: vw(25px);
        left: vw(50.5px);
        top: vw(6px);
        margin-bottom: vw(12px);
        position: relative;
        background-image: url("./assets/spacer.svg");
      }
    }
  }
  > .right {
    position: absolute;
    width: vw(280px);
    height: vw(391px);
    left: vw(1598px);
    top: vw(139px);
    > .variation-label {
      font-family: Montserrat;
      font-size: vw(18px);
      font-style: normal;
      font-weight: 600;
      line-height: vw(22px);
      letter-spacing: 0.02em;
      text-align: right;
      color: #FFFFFF;
      position: relative;
      right: vw(21px);
      top: vw(20px);
    }
    > .variations {
      width: vw(259px);
      height: vw(59px);
      background: rgba(255, 255, 255, 0.05);
      border: vw(1px) solid rgba(255, 255, 255, 0.15);
      box-sizing: border-box;
      position: relative;
      top: vw(30px);
      > .label {
        font-family: Montserrat;
        font-size: vw(12px);
        font-style: normal;
        font-weight: 500;
        line-height: vw(15px);
        letter-spacing: 0em;
        text-align: left;
        position: absolute;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        color: #FFFFFF;
      }
      > .arrow {
        width: vw(29px);
        height: vw(30px);
        position: absolute;
        background-size: 100% 100%;
        top: vw(13px);
        &.right {
          background-image: url("./assets/arr-right.svg");
          right: vw(15px);
        }
        &.left {
          background-image: url("./assets/arr-left.svg");
          left: vw(15px);
        }
      }
    }
    > .total-price {
      font-family: Montserrat;
      font-size: vw(18px);
      font-style: normal;
      font-weight: 600;
      line-height: vw(22px);
      letter-spacing: 0.02em;
      text-align: right;
      color: #FFFFFF;
      position: relative;
      top: vw(70px);
      right: vw(21px);
    }
    > .price-overview {
      width: vw(259px);
      height: vw(187px);
      background: rgba(255, 255, 255, 0.05);
      box-sizing: border-box;
      background: url("./assets/buy-overview.svg");
      background-size: 100% 100%;
      top: vw(80px);
      position: relative;
      > .store-time {
        font-family: Montserrat;
        font-size: vw(12px);
        font-style: normal;
        font-weight: 500;
        line-height: vw(15px);
        letter-spacing: 0.02em;
        text-align: left;
        color: #FFFFFF;
        width: vw(223px);
        position: relative;
        left: vw(18px);
        top: vw(11px);
        > .right {
          float: right;
        }
      }
      > .all-price {
        font-family: Montserrat;
        font-size: vw(14px);
        font-style: normal;
        font-weight: 600;
        line-height: vw(17px);
        letter-spacing: 0em;
        text-align: left;
        color: #FFFFFF;
        position: relative;
        left: vw(18px);
        top: vw(25px);
      }
      > .price {
        font-family: Montserrat;
        font-size: vw(36px);
        font-style: normal;
        font-weight: 600;
        line-height: vw(44px);
        letter-spacing: 0.02em;
        text-align: left;
        color: #2FFF9B;
        position: relative;
        left: vw(16px);
        top: vw(25px);
      }
      > .btns {
        width: vw(223px);
        height: vw(46px);
        position: absolute;
        bottom: vw(26px);
        left: vw(18px);
        display: grid;
        grid-template-columns: repeat(2, vw(109px));
        gap: vw(5px);
        > button {
          width: vw(109px);
          height: vw(46px);
          background: rgba(255, 255, 255, 0.05);
          font-family: Montserrat;
          font-size: vw(12px);
          font-style: normal;
          font-weight: 600;
          line-height: vw(15px);
          letter-spacing: 0.02em;
          text-align: center;
          border: none;
          color: rgba(255, 255, 255, 0.25);
          background: rgba(255, 255, 255, 0.05);
          &.active {
            background: #FFDC5F;
            box-shadow: 0px 4px 40px rgba(255, 220, 95, 0.5);
            color: #252525;
          }
        }
      }
    }
  }
  > .camera-settings {
    width: vw(500px);
    height: vw(114px);
    position: absolute;
    right: vw(63px);
    bottom: vw(62px);
    > .cams {
      position: relative;
      display: grid;
      grid-template-rows: repeat(3, vw(15px));
      gap: vw(15px);
      > .cam {
        float: right;
        > .label {
          font-family: Montserrat;
          font-size: vw(12px);
          font-style: normal;
          font-weight: 500;
          text-align: right;
          color: white;
          position: absolute;
          left: 0;
          width: vw(241px);
          height: vw(15px);
        }
      }
    }
    > .label {
      font-family: Montserrat;
      font-size: 18px;
      font-style: normal;
      font-weight: 600;
      line-height: 22px;
      letter-spacing: 0.02em;
      text-align: right;
      color: #FFFFFF;
      margin-bottom: vw(12px);
    }
  }
}

input[type=range] {
  width: vw(244px);
  margin: 5.5px 0;
  background-color: transparent;
  -webkit-appearance: none;
  float: right;
}
input[type=range]:focus {
  outline: none;
}
input[type=range]::-webkit-slider-runnable-track {
  background: rgba(255, 255, 255, 0.4);
  width: vw(244px);
  height: 4px;
  cursor: pointer;
}
input[type=range]::-webkit-slider-thumb {
  margin-top: -5.7px;
  width: 15px;
  height: 15px;
  background: #ffffff;
  border: 0;
  border-radius: 50px;
  cursor: pointer;
  -webkit-appearance: none;
}
input[type=range]:focus::-webkit-slider-runnable-track {
  background: #ffffff;
}
input[type=range]::-moz-range-track {
  background: rgba(255, 255, 255, 0.4);
  width: vw(244px);
  height: 4px;
  cursor: pointer;
}
input[type=range]::-moz-range-thumb {
  width: 15px;
  height: 15px;
  background: #ffffff;
  border: 0;
  border-radius: 50px;
  cursor: pointer;
}
input[type=range]::-ms-track {
  background: transparent;
  border-color: transparent;
  border-width: 6.5px 0;
  color: transparent;
  width: vw(244px);
  height: 4px;
  cursor: pointer;
}
input[type=range]::-ms-fill-lower {
  background: #f2f2f2;
}
input[type=range]::-ms-fill-upper {
  background: rgba(255, 255, 255, 0.4);
}
input[type=range]::-ms-thumb {
  width: 15px;
  height: 15px;
  background: #ffffff;
  border: 0;
  border-radius: 50px;
  cursor: pointer;
  margin-top: 0px;
  /*Needed to keep the Edge thumb centred*/
}
input[type=range]:focus::-ms-fill-lower {
  background: rgba(255, 255, 255, 0.4);
}
input[type=range]:focus::-ms-fill-upper {
  background: #ffffff;
}
/*TODO: Use one of the selectors from https://stackoverflow.com/a/20541859/7077589 and figure out
how to remove the virtical space around the range input in IE*/
@supports (-ms-ime-align:auto) {
  /* Pre-Chromium Edge only styles, selector taken from hhttps://stackoverflow.com/a/32202953/7077589 */
  input[type=range] {
    margin: 0;
    /*Edge starts the margin from the thumb, not the track as other browsers do*/
  }
}

